import React, { Component } from 'react'
import PropTypes from 'prop-types'
import { connect } from 'react-redux';
import { withRouter } from 'react-router-dom'
import { Form, Input, Button, Checkbox, message } from 'antd';
import { login, tempLogin } from '../../redux/actions';
import LinkButton from '../RenderLinkButton'

const layout = {
    labelCol: { span: 5 },
    wrapperCol: { span: 17 },
};
const tailLayout = {
    wrapperCol: { offset: 2, span: 18 },
};

class LoginAntd extends Component {

    static propTypes = {
        login: PropTypes.func.isRequired
    }

    //
    onFinish = user => {
        const { username, password } = user
        const { login } = this.props
        login(username, password)
    }
    //
    onFinishFailed = data => {
        message.error('' + data.errorFields[0].errors[0])
    }

    //
    touristLogin = () => {
        const { tempLogin } = this.props
        tempLogin()
    }

    render() {
        return (
            <Form
                {...layout}
                name="basic"
                initialValues={{ remember: false }}
                onFinish={this.onFinish}
                onFinishFailed={this.onFinishFailed}
            >
                <Form.Item
                    label=""
                    name="username"
                    rules={[{ required: true, message: '' }]}
                >
                    <Input />
                </Form.Item>

                <Form.Item
                    label=""
                    name="password"
                    rules={[{ required: true, message: '' }]}
                >
                    <Input.Password />
                </Form.Item>

                <Form.Item {...tailLayout} name="remember" valuePropName="checked">
                    <Checkbox></Checkbox>

                </Form.Item>

                <Form.Item {...tailLayout}>
                    <Button type="primary" htmlType="submit">
                        
                    </Button>
                    <LinkButton style={{marginLeft:'20px'}} onClick={this.touristLogin}></LinkButton>
                </Form.Item>

            </Form>
        );
    }
}

export default connect(
    state => ({}),
    { login, tempLogin }
)(withRouter(LoginAntd))