import React, { Component } from 'react'
import { Card, Select, Input, Button, Table, message } from 'antd';
import { PlusOutlined } from '@ant-design/icons'
import { reqProductList, reqProductSearchByDesc, reqProductSearchByName, reqProductStatusUpdate } from '../../api'
import { PAGE_SIZE } from '../../constances'
import LinkButton from '../../Components/RenderLinkButton'

const { Option } = Select
export default class ProductHome extends Component {
    state = {
        loading: false,
        optionValue: 'productName',
        inputValue: '',
        productList: [],
        pageNum: 1,
        total: 0
    }

    //
    getProductLists = async (type = 'all') => {
        let result
        const pageSize = PAGE_SIZE
        const { pageNum, inputValue, optionValue } = this.state
        this.setState({ loading: true })
        ////
        if (inputValue === '') {
            result = await reqProductList({ pageNum, pageSize })
        } else {
            optionValue === 'productDesc' ? (
                result = await reqProductSearchByDesc([inputValue, pageNum, pageSize])
            ) : (
                    result = await reqProductSearchByName({
                        name: inputValue,
                        pageSize,
                        pageNum,
                    })
                )
        }

        this.setState({ loading: false })
        const { list, total } = result
        if (result && list) {
            this.setState({
                productList: list,
                total
            })
        } else message.error('~')
    }

    componentDidMount() {
        //
        this.columns = [
            {
                title: '',
                width: 264,
                dataIndex: 'name',
            },
            {
                title: '',
                width: 548,
                dataIndex: 'desc',
            },
            {
                title: '',
                dataIndex: 'price',
                width: 240,
                render: price => <span>{price}</span>
            },
            {
                title: '',
                width: 240,
                render: product => (
                    <div>
                        <Button
                            style={{ marginRight: 20 }}
                            // 
                            onClick={async () => {
                                let { id, status } = product
                                status = status ? 0 : 1
                                const result = await reqProductStatusUpdate(id, { status })
                                if (result !== 1) message.error('~')
                                else this.getProductLists()
                            }}
                        >
                            {product.status ? '' : ''}
                        </Button>
                        <span>{product.status ? '' : ''}</span>
                    </div>
                )
            },
            {
                title: '',
                width: 240,
                render: product => (
                    <div>
                        <LinkButton onClick={() => { this.props.history.push('/product/create', { product }) }}></LinkButton>
                        <LinkButton onClick={() => { this.props.history.push('/product/retrieve', { product }) }}></LinkButton>
                    </div>
                )
            },
        ];

        //
        this.getProductLists()
    }

    render() {
        const { optionValue, productList, total, loading, pageNum } = this.state
        const title = (
            <div>
                <Select
                    value={optionValue}
                    style={{ width: 150 }}
                    onChange={optionValue => { this.setState({ optionValue }) }}
                >
                    <Option value='productName'></Option>
                    <Option value='productDesc'></Option>
                </Select>
                <Input
                    placeholder=''
                    style={{ width: 150, margin: '0 15px' }}
                    onChange={event => { this.setState({ inputValue: event.target.value }) }}
                    onPressEnter={() => {
                        this.setState({
                            pageNum: 1
                        }, () => {
                            this.getProductLists('search')
                        })
                    }}
                />
                <Button
                    type='primary'
                    //
                    onClick={() => {
                        this.setState({
                            pageNum: 1
                        }, () => {
                            this.getProductLists('search')
                        })
                    }}
                >
                    
                </Button>
            </div>
        )
        const extra = (
            <Button type='primary' onClick={() => { this.props.history.push('/product/create', { product: {} }) }}>
                <PlusOutlined />
                
            </Button>
        )
        return (
            <Card title={title} extra={extra}>
                <Table
                    loading={loading}
                    bordered
                    rowKey='id'
                    dataSource={productList}
                    columns={this.columns}
                    pagination={{
                        current: pageNum,
                        pageSize: PAGE_SIZE,
                        showQuickJumper: true,
                        total: total,
                        simple: true
                    }}
                    //
                    onChange={event => {
                        this.setState({
                            pageNum: event.current
                        }, () => {
                            //
                            this.state.inputValue ? this.getProductLists('search') : this.getProductLists()
                        })
                    }}
                />
            </Card>
        )
    }
}

