import React, { Component } from 'react'
import { Card, Button, Table, Modal, Form, Input, message, Select } from 'antd';
import { PlusOutlined, ArrowRightOutlined } from '@ant-design/icons';
import LinkButton from '../../Components/RenderLinkButton'
import { reqRetrieveCategory, reqUpdateCategory, reqCreateCategory } from '../../api'

const { Option } = Select

export default class Category extends Component {
    state = {
        loading: false,
        primaryCategory: [],
        secondaryCategory: [],
        currentCategory: {
            id: '',                                  //id
            name: '',                                //
            parentId: 0,
            parentName: '',
            createParentId: 0,
            createParentName: ''
        },
        visible: 0                   //012
    }
    //idname
    getNameById = (parentId) => {
        const list = this.state.primaryCategory
        const { name } = list.find(item => item.id === parentId)
        return name
    }

    ///
    getCategory = async () => {
        this.setState({ loading: true })
        const { parentId } = this.state.currentCategory
        const result = await reqRetrieveCategory(parentId)
        this.setState({ loading: false })
        if (result.status === 0) {
            parentId === 0 ? this.setState({ primaryCategory: result.data }) : this.setState({ secondaryCategory: result.data })
        }
    }
    //
    getSecondaryCategory = category => {
        const { id: parentId, name: parentName } = category
        this.setState({
            currentCategory: {
                id: '',
                name: '',
                parentId,
                parentName,
                createParentId: parentId,
                createParentName: parentName
            },
        }, this.getCategory)
    }
    //
    backToPrimaryCategory = () => {
        this.setState({ loading: true }, () => {
            setTimeout(() => {
                this.setState({
                    loading: false,
                    secondaryCategory: [],
                    currentCategory: {
                        id: '',                                  //id
                        name: '',                                //
                        parentId: 0,
                        parentName: '',
                        createParentId: 0,
                        createParentName: ''
                    },
                })
            }, 80)
        })
    }

    //
    showCreateModal = () => {
        this.setState({
            visible: 1,
        })
    }
    //
    showUpdateModal = category => {
        const { id, name } = category
        this.setState(state => {
            return {
                visible: 2,
                currentCategory: {
                    ...state.currentCategory,
                    id,
                    name
                }
            }
        })
        //
        this.formNode.setFieldsValue({
            name: category.name
        })
    }

    //
    handleCancel = () => {
        this.setState(state => {
            return {
                visible: 0,
                currentCategory: {
                    ...state.currentCategory,
                    id: '',
                    name: '',
                    createParentId: state.currentCategory.parentId,
                    createParentName: state.currentCategory.parentName
                }
            }
        })
        //
        this.formNode.setFieldsValue({
            name: ''
        })
    }

    //
    handleSubmit = async () => {
        //
        let { visible, currentCategory: { id, parentId, createParentId, createParentName } } = this.state
        const { name } = this.formNode.getFieldValue()
        let result = {}

        //
        try {
            await this.formNode.validateFields()
        } catch (e) {
            message.error('')
            return
        }

        //
        if (visible === 1) {
            result = await reqCreateCategory({
                parentId: createParentId,
                name,
                parentName: createParentName,
                categoryName: createParentId === 0 ? '' : ''
            })
        } else if (visible === 2) {
            result = await reqUpdateCategory({
                id,
                name,
                parentId: createParentId,
                parentName: createParentName,
                categoryName: createParentId === 0 ? '' : ''
            })
        }

        //
        if (result.status !== 0) {
            message.error('~')
        } else {
            message.success('')
            this.handleCancel()
            //
            if (createParentId === parentId || createParentId === 0) this.getCategory()
        }
    }

    componentDidMount() {
        //
        this.columns = [
            {
                title: '',
                dataIndex: 'name',
                key: 'name',
            },
            {
                title: '',
                width: 300,
                dataIndex: 'operation',
                key: 'operation',
                render: (_, category) => (
                    <div>
                        <LinkButton onClick={() => { this.showUpdateModal(category) }}></LinkButton>
                        {this.state.currentCategory.parentId !== 0 ? null : (
                            <LinkButton onClick={() => this.getSecondaryCategory(category)}></LinkButton>
                        )}
                    </div>
                )
            },
        ];
        //
        this.getCategory()
    }
    render() {
        const { primaryCategory, secondaryCategory, loading, visible, currentCategory: { parentId, parentName, createParentId } } = this.state
        const title = parentId === 0 ? '' : (
            <div>
                <LinkButton onClick={this.backToPrimaryCategory}></LinkButton>
                <ArrowRightOutlined />
                <span>{parentName}</span>
            </div>
        )
        return (
            <div>
                {/*  */}
                <Card title={title} extra={(
                    <Button type='primary' onClick={this.showCreateModal}>
                        <PlusOutlined />
                            
                    </Button>
                )}>
                    <Table
                        bordered
                        loading={loading}
                        dataSource={parentId === 0 ? primaryCategory : secondaryCategory}
                        columns={this.columns}
                        rowKey='id'
                        pagination={{
                            showSizeChanger: true,
                            showQuickJumper: true,
                        }}
                    />;
                </Card>
                {/*  */}
                <Modal
                    forceRender={true}
                    title={visible === 1 ? '' : ''}
                    visible={visible === 0 ? false : true}
                    onOk={this.handleSubmit}
                    onCancel={this.handleCancel}
                >
                    {visible === 1 ? (
                        <Form ref={c => { this.formNode = c }}>
                            <Form.Item>
                                
                                <Select
                                    value={createParentId}
                                    onChange={createParentId => {
                                        this.setState(state => {
                                            return {
                                                currentCategory: {
                                                    ...state.currentCategory,
                                                    createParentId,
                                                    createParentName: this.getNameById(createParentId)
                                                }
                                            }
                                        })
                                    }}
                                >
                                    <Option value={0}></Option>
                                    {primaryCategory.map(item => <Option key={item.id} value={item.id}>{item.name}</Option>)}
                                </Select>
                            </Form.Item>
                            <span></span>
                            <Form.Item
                                name='name'
                                rules={[{ required: true, message: '' }]}
                            >
                                <Input
                                    placeholder=''
                                    allowClear={true}
                                    onPressEnter={this.handleSubmit}
                                />
                            </Form.Item>
                        </Form>
                    ) : (
                            <Form ref={c => { this.formNode = c }}>
                                <Form.Item
                                    name='name'
                                    rules={[{ required: true, message: '' }]}
                                >
                                    <Input
                                        placeholder=''
                                        allowClear={true}
                                        onPressEnter={this.handleSubmit}
                                    />
                                </Form.Item>
                            </Form>
                        )}
                </Modal>
            </div>
        )
    }
}
