import React, { Component } from 'react'
import { Card, Table, Button, Modal, message } from 'antd'
import { ExclamationCircleOutlined } from '@ant-design/icons'
import { reqRoleList, reqUserList, reqDeleteUser } from '../../api'
import LinkButton from '../../Components/RenderLinkButton'
import UserForm from './user-form'
import { putServerTime } from '../../utils/transferTime'

const { confirm } = Modal
export default class User extends Component {
    state = {
        users: [],
        roles: [],
        visible: false,
        user: {}
    }

    //
    getUserList = async () => {
        //
        const users = await reqUserList()
        if (users instanceof Array && users.length > 0) {
            this.setState({
                users
            })
        } else message.error('')
        //
        const result = await reqRoleList()
        const { status, data } = result
        if (status === 0) this.setState({ roles: data })
        else message.error('')
    }

    //
    handleCancel = (reload = false) => {
        this.setState({
            visible: false,
            user: {}
        })
        if (reload) this.getUserList()
    }

    //
    showConfirm = user => {
        const _this = this
        const confirmNode = confirm({
            title: '',
            icon: <ExclamationCircleOutlined />,
            content: '',
            async onOk() {
                const result = await reqDeleteUser(user.id)
                if (result === 'success') {
                    message.success('')
                    _this.getUserList()
                } else message.error('')
                confirmNode.destroy()
            },
        });
    }

    componentDidMount() {
        //
        this.getUserList()
        //table
        this.columns = [
            {
                title: '',
                dataIndex: 'name'
            },
            {
                title: '',
                dataIndex: 'email'
            },
            {
                title: '',
                dataIndex: 'phone'
            },
            {
                title: '',
                dataIndex: 'createTime',
                render: putServerTime
            },
            {
                title: '',
                dataIndex: 'roleId',
                render: roleId => {
                    const { roles } = this.state
                    const role = roles.filter(item => roleId * 1 === item.id)
                    if (role.length > 0) return role[0].name
                }
            },
            {
                title: '',
                render: user => {
                    return (
                        <span>
                            <LinkButton onClick={() => {
                                this.setState({
                                    user,
                                    visible: true
                                })
                            }}>
                                
                            </LinkButton>
                            <LinkButton onClick={() => { this.showConfirm(user) }}></LinkButton>
                        </span>
                    )
                }
            }
        ]
    }
    render() {
        const { users, roles, visible, user } = this.state
        //
        const title = (
            <Button type='primary' onClick={() => { this.setState({ visible: true }) }}>
                
            </Button>
        )
        return (
            <Card title={title}>
                <Table
                    dataSource={users}
                    columns={this.columns}
                    rowKey='id'
                >
                </Table>
                <UserForm visible={visible} user={user} roles={roles} handleCancel={this.handleCancel} />
            </Card>
        )
    }
}
